/* ================= BASE ================= */

:root {
  --bg: #ffffff;
  --text-color: #121212;
  --muted: #555;
  --accent-color: #4dabf7;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  background: var(--bg);
  color: var(--text-color);
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* ================= NAVBAR ================= */

.navbar-custom {
  font-size: large;
}

.navbar__link {
  margin-left: 0.75em;
  margin-right: 0.75em;
  font-size: 1em;
  cursor: pointer;
  text-decoration: none;
  letter-spacing: 0.1em;
  text-indent: 0.3em;
  border-bottom: 3px solid transparent;
  color: white;
}

.navbar__link::after {
  transition: all ease-in-out 0.2s;
  content: "";
  display: block;
  margin-top: 2px;
  height: 3px;
  width: 0;
  background-color: white;
}

.navbar__link:hover::after {
  width: 40%;
}

.navbar__link--active::after {
  width: 100%;
}

/* ================= MAIN LAYOUT ================= */

.MainApp {
  margin-top: 8vh; /* fixed navbar height */
  flex: 1;
}

.main {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 8vh);
}

/* â— IMPORTANT:
   Do NOT vertically center content here
   This was hiding the About heading earlier */
.section-content-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  width: 100%;
}

/* ================= TYPEWRITER ================= */

.Typewriter {
  display: inline-block;
}

.Typewriter__wrapper,
.Typewriter__cursor {
  font-size: xx-large;
  font-weight: bold;
}

/* ================= SOCIAL ================= */

.social {
  margin-top: 2.5rem;
}

/* ================= HEADER ================= */

.header {
  font-size: 3em;
  font-weight: bold;
  margin-bottom: 25px;
  text-align: center;
}

/* ================= ABOUT PAGE ================= */

.about-section {
  width: 100%;
  display: flex;
  justify-content: center;

  /* keeps content below fixed navbar */
  padding-top: calc(8vh + 2.5rem);
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 3rem;
}

.about-grid {
  max-width: 1100px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}

.about-left h2,
.about-right h2 {
  font-size: 2.4rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.about-left p {
  line-height: 1.75;
  margin-bottom: 1rem;
  color: #333;
}

.education-item {
  margin-bottom: 1.5rem;
}

.education-item h3 {
  margin-bottom: 0.3rem;
}

/* ================= TIMELINE ================= */

section.timeline-card-content {
  justify-content: center;
}

section.timeline-card-content p.card-title {
  font-size: 1.5em;
  margin-top: 25px;
}

section.timeline-card-content p.card-sub-title {
  font-size: 1.25em;
  margin-top: 10px;
}

section.timeline-card-content div.card-description > p {
  font-size: 1.1em;
  margin-top: 10px;
  font-weight: 500;
}

/* ================= RESPONSIVE ================= */

@media (max-width: 900px) {
  .about-grid {
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }
}

@media (max-width: 768px) {
  .navbar__link::after {
    display: none;
  }
}
